body {
  background-color: black;
}

.hero_section {
  height: 446px;
  background-image:
  linear-gradient(to bottom, rgba(13, 13, 13, 0.19), rgba(14, 14, 14, 0.8)),
  // linear-gradient(to bottom, rgba(11, 11, 11, 0.5), rgba(11, 11, 11, 0.5)),  
  url(.././images/header/hero-wallpaper_dark-mode.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0 auto;
}

.hero_logo {
  position: absolute;
  top: 0px;
  width: 237px;
  height: 190px;
}

.hero_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero_title {
  font-family: Montserrat;
  font-size: 50px;
  font-weight: 900;
  line-height: 1.219;
  color: rgba(220, 86, 197, 1);
  margin-top: 40px;
  text-align: center;
  width: 608px;
  height: 122px;
}

.hero_search-filter_container {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin-top: 48px;
}

.hero-search {
  .search {
    width: 288px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    padding: 10px 60px 10px 32px;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;
    color: rgba(147, 147, 147, 1);
    border-radius: 20px;
    border: none;
    transition: box-shadow 0.3s ease;
  }
}

.hero-search {.search:hover {
  width: 288px;
  height: 40px;
  border-radius: 20px;
  box-shadow: 0 0 15px 5px #ff78d6;
  outline: none;
}}

.hero-filter {.country-input:hover {
  border-radius: 20px;
  box-shadow: 0 0 15px 5px #ff78d6;
  outline: none;
}}

.hero-search {
  .search::placeholder {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;
    color: rgba(147, 147, 147, 1);
    border-radius: 20px;
  }
}

.hero-search {
  position: relative;
}

.btn-search-svg {
  position: absolute;
  top: 14px;
  right: 32px;
  fill: #939393;
  transition: fill 0.2s ease, transform 0.2s ease;
}

.btn-search-svg:hover {
  fill: #ff78d6;  
  transform: scale(1.5);
}

.hero-filter {
  .country-input {
    width: 288px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    padding: 10px 60px 10px 32px;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;
    color: rgba(147, 147, 147, 1);
    border-radius: 20px;
    transition: box-shadow 0.3s ease;
  }
}

.hero-filter {
  .country-input::placeholder {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;
    color: rgba(147, 147, 147, 1);
    border-radius: 20px;
  }
}

.hero-filter {
  position: relative;
}

// .hero-filter:hover .btn-filter {
//   fill: #ff78d6;
// }

.btn-filter {
  position: absolute;
  top: 15px;
  right: 32px;
}

.hero-filter {
  .country-container {
    display: none;
    color: #fff;
    width: 288px;
    min-height: 20px;
    max-height: 165px;    
    backdrop-filter: blur(20px);
    z-index: 15;
    background: #fff3;
    border-radius: 25px;
    position: absolute;
    top: 45px;
    overflow: hidden;
    box-shadow: 1px 3px 24px -5px #ff56c5b0;
  }}

  .country-container.visible {
    display: block;
  }

  .country-item {
    text-align: left;
    color: #ccc;
    cursor: pointer;
    padding: 4px 10px 4px 30px;
    font-size: 16px;
    line-height: 19.5px;
  }

  .country-item:hover {
    color: #ff78d6;
  }

////////////////////////////////   Scrollbar Customization   ////////////////////////////////

.hero-filter .country-container .country-list {
  overflow-y: scroll;
  max-height: 165px;
  padding-right: 8px;
  border-radius: 25px;
  box-sizing: content-box;
}

.hero-filter .country-container .country-list::-webkit-scrollbar {
  width: 8px;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 25px;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-thumb {
  background-color: #939393;
  border-radius: 25px;
  border: 2px solid transparent;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-thumb:hover {
  background-color: #ff78d6;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-button:start:increment {
  height: 12%;
  display: block;
  background: transparent;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
  height: 12%;
  display: block;
  background: transparent;
}

@media (min-width: 768px) and (max-width: 1280px) {

  .hero_section {
      height: 364px;
    }
  
  .hero_logo {
    width: 180px;
    height: 150px;
  }

  .hero_title {
    font-size: 30px;
    line-height: 1.219;
    width: 400px;
    height: 74px;
  }

  .hero-search {
    .search {
      width: 336px;
      height: 40px;
    }
  }
    .hero-filter {
    .country-input {
      width: 336px;
      height: 40px;
    }
  }

  .hero-filter {
    .country-container {
      width: 336px;
    }
  }

  .hero_search-filter_container {
    margin-top: 40px;
  }

  .hero-filter {
    .country-container {
      max-height: 140px;
    }
  }

  
  .hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
    height: 30%;
  }

  .back-to-the-top {display: none;}
}

@media (max-width: 768px) {

  .hero_section {
      height: 364px;
    }

  .hero_title {
    margin-top: 80px;
  }

  .hero_logo {
    width: 106px;
    height: 90px;
  }

  .hero_title {
    font-size: 18px;
    line-height: 1.219;
    width: 275px;
    height: 44px;
  }

  .hero-search {
    .search {
      width: 280px;
      height: 40px;
    }
  }
  
  .hero-filter {
    .country-input {
      width: 280px;
      height: 40px;
    }
  }

  .hero-filter {
    .country-container {
      width: 280px;
    }
  }

  .hero_search-filter_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    margin-top: 24px;
  }

  .hero-filter {
    .country-container {
      max-height: 80px;
    }}

  .hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
    height: 65%;
  }

  .back-to-the-top {display: none;}
}


////////////////////// Back to the top //////////////////////
.back-to-the-top {
  width: 70px;
  height: 70px;
  border: 3px solid #ff78d6;
  background-color: #ff78d6;
  border-radius: 50%;
  position: fixed;
  bottom: 30px;
  right: 30px;
  opacity: 0%;
  z-index: 100;
  transition: opacity 0.3s;
  cursor: pointer;
}

.back-to-the-top_svg {
  fill: white;
}

